<template>
  <DataGrid :data="config" @editEnd="edit" groupField="group" :clickToEdit="true" selectionMode="cell" editMode="cell" :border="false">
    <template slot="group" slot-scope="scope" >
      <span style="font-weight:bold;">
        {{scope.value}}
      </span>
    </template>
    <GridColumn field="label" title="" width="100"></GridColumn>
    <GridColumn field="value" title="" :editable="true"></GridColumn>
  </DataGrid>
</template>

<script>
export default {
  name: 'workflowConfig',
  props: ['attributes', 'update'],
  data() {
    return {
      
    }
  },
  computed:{
    config(){
      return [{
        group:'',
        label:'',
        code:'label',
        value:this.attributes.label
      },{
        group:'',
        label:'key',
        code:'workflowKey',
        value:this.attributes.workflowKey
      },{
        group:'',
        label:'',
        value:''
      },{
        group:'',
        label:'',
        value:''
      }]
    }
  },
  methods: {
    edit(data){
      this.update(data.row.code, data.row.value)
    }
  },
  watch:{
    
  },
  mounted(){

  }
}
</script>


